@tailwind base;
@tailwind components;
@tailwind utilities;

.dashboard-container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
  }
  
  /* Ensure labels and charts align horizontally with no overflow issues */
  .chart-container {
    display: flex;
    width: 100%;
    align-items: flex-start; /* Align to top */
    gap: 12px;
  }
  
  /* Labels take a fixed amount of space */
  .labels-container {
    width: 150px; /* Adjust width as needed */
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* Spread labels evenly */
    padding-right: 12px;
  }
  
  /* Each label matches the height of its bar */
  .label-item {
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: flex-end; /* Align text to the right */
    white-space: nowrap;
  }
  
  /* The chart takes the remaining space */
  .bar-chart-wrapper {
    flex: 1;
    min-width: 0;
  }
  
  /* Override Tailwind padding and margin defaults that may interfere */
  *,
  ::before,
  ::after {
    padding: 0 !important;
    margin: 0 !important;
    box-sizing: border-box;
  }
  